export default {
	props: {
		top: {
			// 距离顶部距离, 单位upx
			type: Number,
			default: 0
		}
	},
	data() {
		return {
			pageDeviation: 0,
			pageTransition: 0,
			startY: 0,
			moveY: 0,
			touchend: false
		}
	},
	computed: {
		pageTop() {
			return uni.upx2px(this.top);
		}
	},
	methods:{
		handleTouchstart(e){
			
			this.touchend = false;
			this.pageTransition = 0;
			this.startY = e.touches[0].pageY;
		},
		handleTouchmove(e){
			
			if(this.touchend){
				return;
			}
			
			this.moveY = e.touches[0].pageY - this.startY;
			
			if(this.moveY >= 0){
				this.pageDeviation = this.moveY;
			}
		},
		handleTouchend(e){
			
			this.touchend = true;
			if(this.moveY == 0){
				return;
			}
			
			this.pageTransition = 0.3;
			
			if(this.moveY >= 50){
				// 加载数据
				this.startPulldownRefresh();
			}else {
				this.pageDeviation = 0;
			}
			
			this.startY = this.moveY = 0;
		},
		startPulldownRefresh(){
			// console.log("加载数据");
			this.pageDeviation = uni.upx2px(90);
			// 加载数据
			this.$emit('refresh');
		},
		endPulldownRefresh(){
			this.pageDeviation = uni.upx2px(0);
		}
	}
}
